/**
 * æ ¹æ®shortcodeæ„å»ºä¼šè®®URL
 */
export function buildMeetingUrl(shortCode: string, role: 'initiator' | 'responder', eventType?: string): string {
  const baseUrl = '/join-call';
  let url = `${baseUrl}?code=${shortCode}&role=${role}`;
  if (eventType) {
    url += `&eventType=${eventType}`;
  }
  return url;
}

/**
 * æ ¼å¼åŒ–ä¼šè®®åˆ›å»ºæ—¶é—´
 */
export function formatMeetingTime(timestamp: number): string {
  const date = new Date(timestamp * 1000);
  const now = new Date();
  const diffMs = now.getTime() - date.getTime();
  const diffMins = Math.floor(diffMs / (1000 * 60));
  const diffHours = Math.floor(diffMins / 60);

  if (diffMins < 1) {
    return 'åˆšåˆš';
  } else if (diffMins < 60) {
    return `${diffMins}åˆ†é’Ÿå‰`;
  } else if (diffHours < 24) {
    return `${diffHours}å°æ—¶å‰`;
  } else {
    return date.toLocaleDateString('zh-CN', {
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  }
}

